import React, { useEffect, useState } from 'react';
import { TonClient } from 'ton-client-js';
import { TonConnect } from '@tonconnect/sdk';

function App() {
    const [client, setClient] = useState(null);
    const [connected, setConnected] = useState(false);

    useEffect(() => {
        const initClient = async () => {
            try {
                const tonClient = new TonClient({ network: { server_address: "https://test.ton.dev" } });
                await tonClient.setup();
                setClient(tonClient);
            } catch (error) {
                console.error("Failed to initialize TON Client", error);
            }
        };

        initClient();
    }, []);

    const handleConnect = async () => {
        try {
            const tonConnect = new TonConnect({ bridgeUrl: 'https://tonconnect.tonhubapi.com/tonconnect' });
            await tonConnect.connect();
            setConnected(true);
            console.log('Connected to TON Wallet');
        } catch (error) {
            console.error("Failed to connect to TON Wallet", error);
        }
    };

    return (
        <div className="App">
            <h1>Pig Mini App - Domain Sales</h1>
            <button onClick={handleConnect}>Connect to TON Wallet</button>
            {connected && <p>Connected to TON Wallet</p>}
        </div>
    );
}

export default App;
